.main-container {
  margin: 0 auto;
  background: rgb(206, 194, 194);
  height: calc(100vh);
}
.header-bar {
height: 72px;
}
.gameLogo{
font-size: 108%;
line-height: 2.2;
color: rgba(255, 255, 255, 0.7);
z-index: 1;
}
.callout{
position: relative;
width: 68px;
height: 64px;
border-radius: 0 50% 50% 0;
background: rgba(0, 0, 0, 0.7);
margin: -1% 0 0 -2%;
}
.callbg {
position: absolute;
left: -18%;
width: 105%;
}
.callball{
position: absolute;
left: -3%;
top: 10%;
width: 83%;
}
.userDetails{
font-size: 140%;
color: white;
text-align: center;
z-index: 1;
}
.housie .userDetails {
text-align: right;
}
.onlinePlayer {
position: absolute;
right: 5%;
color: #fff;
font-size: 110%;
top: 6%;
}
.calledNumber {
color: #fff;
background: #d10000;
font-size: 200%;
width: 52px;
height: 52px;
border-radius: 50%;
letter-spacing: -0.05rem;
font-weight: bold;
margin: 6px 0px 0 10px;
text-align: center;
margin-left: -50px;
}
.showCalledNumber {
  margin-left:10px;
  transition: 1s all ease;
}
.menuIcon{
  z-index: 1;
}
.menuIcon img {
float: right;
/* width: 46%; */
width: 36px;
margin-top: 5px;
margin-right: 12px;
}
.housie .menuIcon img{
  width: 36px;
  margin-top: 5px;
  margin-right: -3px;
}
.shuffleIcon {
  text-align: center;
  transform: scale(0);
}
.shuffleIcon img{
  width :47%;
}
.playIcon {
  text-align: center;
  transform: scale(0);
}
.playIcon img{
  width :61%;
}
.onlineText {
  text-align: center;
  font-size: calc(0.5rem + 1vh);
  color:white;
  transform: scale(0);

}
.selectTitle{
  font-size: 2rem;
  color: white;
  text-align: center;
  font-weight: 500;
}
.ticket-number {
  width: 100%;
  height: 100%;
  border: 1px solid #ffca63;
  background-color: white;
  text-align: center;
  align-items: center;
  padding:0% 1% !important;
}
.ticketRow {
  height: 33.5%;
}

.ticket-number span{
  font-size: 140%;
  font-weight: 500;
  display: flex;
  height: 100%;
  margin: 0 auto;
  line-height: 4.5vh;
  letter-spacing: -0.05rem;
  justify-content: center;
}
.tickerTitle {
  text-transform: uppercase;
  letter-spacing: 0px;
  font-size: 105%;
  transform: rotate(270deg);
  font-weight: 500;
  position: absolute;
  left: -18%;
  top: 33%;
  color: white;
}
.ticketLabel {
  position: absolute;
  left: -9.5%;
  font-size: 80%;
  bottom: 0;
  font-weight: 500;
  letter-spacing: -0.1rem;
  bottom: -4%;
  font-weight: 600;
}

.called {
  transition: all .5s ease-in-out;
  background-color: #e5e05e;
}
.number-wrapper {
  width: 100%;
}

.ticket-wrapper {
  margin-right: 2%;
  background-size: 100% 100%;
  height: 18%;
  border: 10px solid #ffca63;
  border-radius: 3vh;
  border-left-width: 5vh;
  margin-left: 3%;
  margin-top: 0%;
  position: relative;
  background-color: #ffca63;
  opacity: 0;

}
.housie-wrapper {
  width: 100%;
  height: 39%;
  position: relative;
  top: 0%;
}
.housieRow {
  width: 100%;
  display: flex;

  /* height: 11%; */
  /* margin-bottom: 3px !important; */
}
.housie-number, .housie-number-called {
  width: 28px;
  height: 28px;
  border: 2px solid #d10000;
  background-color: #082657;
  text-align: center;
  border-radius: 50%;
  margin: 1px auto !important;
  padding: 0 !important;
}
.housie-number span, .housie-number-called span {
  font-size: 100%;
  font-weight: bold;
  line-height: 1.5;
  color: #5688e9;
  letter-spacing: -0.05rem;
}
.housie-number-called {
  background-color: #d10000;
}
.housie-number-called span {
  color: #fff;
}
.first-ticket-row > div:last-child {
  border-top-right-radius: 2vh;
}
.last-ticket-row > div:last-child {
  border-bottom-right-radius: 2vh;
}
.first-ticket-row > div:first-child {
  border-top-left-radius: 2vh;
}
.last-ticket-row > div:first-child {
  border-bottom-left-radius: 2vh;
}

.ticket-container > div:first-child {
  padding: 10px 0;
}
.gamebg {
  position: absolute;
  background-image: url(../assets/img/phone_bg.png);
  background-size: 100% 100%;
  height: 100%;
  width: 100%;
  padding:2%;
}

.ticketbg {
  background-image: url(../assets/img/ticket_bg.png);
}
.blankTicketNumber {
  background-color: #ffeecd;
}
.bottomBar{
  color: white;
  position: fixed;
  height: 64px;
  bottom: 0px;
  width: 100%;
  text-align: center;
  align-items: center;
  background-color: #451c74;
  display: flex;
  padding: 0 15px;
}
.bottomBar .col{
  border-radius: 50px;
  /* margin: 4%; */
  font-size: 140%;
  line-height:2;
}
.bottomBar .col.selected{
  background-color: #d2d1e3;
  color:#451c74;
  font-weight: 500;
}
.betCard {
  position: absolute;
  bottom: 64px;
  background-color: #e8edfc;
  /* height: 28%; */
  width: 98%;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  left: 1%;
  box-shadow: 0px 0 10px rgba(69, 28, 116, 0.8);
  padding-bottom: 8px;
}
.betCard2 {
  position: absolute;
  bottom: 208px;
  background-color: #e8edfc;
  height: 64px;
  width: 98%;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  left: 1%;
  box-shadow: 0px 0 10px rgba(69, 28, 116, 0.8);
}
.betCard3 {
  position: absolute;
  bottom: 248px;
  background-color: #e8edfc;
  height: 64px;
  width: 98%;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  left: 1%;
  box-shadow: 0px 0 10px rgba(69, 28, 116, 0.8);
}
.cardTitle {
  font-size: 135%;
  line-height: 1.7;
  font-weight: 500;
  color:#48108d;
}
.cardTopImg{
  margin-top: 2%;
  width: 71px;
  margin-left: 50%;
  height: 37px;
}
.cardTopImg2 {
  margin-left: 44%;
  margin-top: 2%;
  width: 71px;
  height: 37px;
}
.cardTopImg3 {
  margin-left: 40%;
  margin-top: 2%;
  width: 71px;
  height: 37px;
}
.betTitle {
  background-color:#e1d4f4;
  padding: 1%;
  text-align: center;
  font-size: 110%;
}
.betTitle img {
  max-height: 20px;
}
.betValues {
  font-size:1.5rem;
  position: relative;
}
.betValues .betvalue {
  position: absolute;
  left:17%;
  top:16%;
  font-size: 70%;
  line-height: 1.7;
}
.betValues img {
  width:100%;
}
.betValues .betTicketValue {
  padding-top:2%;
  font-size: 70%;
  white-space: nowrap;
  width: 100%;
  text-overflow: ellipsis;
  overflow-x: hidden;
}
.betValues .betTicketValueRs {
  margin-top:1%;
  text-align: center;
  font-size: 90%;
}
.betTitle .back {
  font-size:1.5rem;
  margin-left: 20%;
}
.betTitle .paysTitle {
  font-size:1.5rem;
  margin-left: 27%;
}
.betModal {
  background-color: #f0edff;
  border-top-left-radius: 21px;
  border-top-right-radius: 21px;
  border-bottom-right-radius: 21px;
  border-bottom-left-radius: 21px;
}
.betModal .modal-content {
  background-color:transparent !important;
  border:0px !important;
}
.betModalBody {
  background-color: #92c9ff;
  border-top-left-radius: 21px;
  border-top-right-radius: 21px;
  padding-bottom: 1%;
  border-bottom-right-radius: 21px;
  border-bottom-left-radius: 21px;
}
.betLayModalBody {
  background-color: #ffb7d9;
  border-top-left-radius: 21px;
  border-top-right-radius: 21px;
  padding-bottom: 1%;
  border-bottom-right-radius: 21px;
  border-bottom-left-radius: 21px;
}
.betLayModalBody .backlayTitle {
  font-size: 150%;
  text-align: center;
  font-weight: bold;
  color: #005894;
}
.betLayModalBody .backlayTitle .layTitle{
  padding: 2%;
  color:#8f1b98;
}
.betLayModalBody .backlayTitle .backTitle{
  background-color: #005894;
  color: #fff;
  padding: 2%;
  border-top-left-radius: 20px;
}
.modaltitle{
  text-align: center;
}
.bettitle {
  text-align: center;
}
.bettitle .title {
  font-size: 2rem;
  font-weight: bold;
  color: #005894;
  letter-spacing: 2px;
}
.betLayModalBody .bettitle .title {
  font-size: 150%;
  font-weight: bold;
  color: #d2519f;
  letter-spacing: 2px;
}
.modaltitle .title{
  font-size: 150%;
  font-weight: bold;
  color: #4c1f90;
  letter-spacing: 1px;
  margin-bottom: 8px;
}
.betModalBody .backlayTitle {
  font-size: 150%;
  text-align: center;
  font-weight: bold;
  color: #005894;
}
.betModalBody .backlayTitle .backTitle{
  padding: 2%;
}
.betModalBody .backlayTitle .layTitle{
  background-color: #d2519f;
  color: #fff;
  padding: 2%;
  border-top-right-radius: 20px;
}
.modal-body .ticketRow{
  font-size: 130%;
  text-align: center;
  font-weight: 500;
  color:#005995;
}
.modal-body .ticketRow .val {
  font-size: 180%;
  line-height: 1;
}
.betLayModalBody .ticketRow {
  color:#67176d;
}
.modal-body .ticketRow .ticket1{
  margin: 3%;
  border-radius: 15px;
  background-color: #fff;
  line-height: 1;
  padding: 2%;
}
.modal-body .ticketRow .ticket2{
  margin: 3%;
  border-radius: 15px;
  background-color: #c9e4ff;
  line-height: 1;
  padding: 2%;
}
.betLayModalBody .ticketRow .ticket2{
  background-color: #ffdbec;
}
.fotterRow {
  font-size: 180%;
  text-align: center;
  font-weight: 500;
  color:#4c1e8f;
}
.fotterRow .cancelbtn {
  margin: 1%;
  border-radius: 15px;
  border:2px solid #4c1e8f; 
}
.fotterRow .submitbtn {
  margin: 1%;
  border-radius: 15px;
  background-color: #4c1e8f;
  color:#fff;
}

.betticketRow {
  font-size: 2.2rem;
  text-align: center;
  font-weight: 500;
  color:#005995;
}
.betLayModalBody .betticketRow {
  color:#67176d;
  font-size: 170%;
}
.betticketRow .betticket1 {
  margin: 1% 5%;
  border-radius: 30px;
  background-color: #fff;

}
.betticketRow .betticket2 {
  margin: 1% 5%;
  border-radius: 30px;
  background-color: #c9e4ff;
}
.betLayModalBody .betticketRow .betticket2 {
  margin: 1% 5%;
  border-radius: 30px;
  background-color: #fff;
}
.rowDivider {
  height: 2px;
  width: 110%;
  background-color: #005894;
}
.betLayModalBody .rowDivider {
  height: 2px;
  width: 110%;
  background-color: #d2519f;
}
.globalmenu{
  position: fixed;
  width: 60%;
  background-color: #14133c;
  height: 100vh;
  margin-top: -2%;
  right: 0px;
  z-index: 1;
  text-align: center;
  transition:right .5s;
  opacity: 0;
  z-index: 2;
}
.globalmenu .menutitle {
  font-size: 150%;
  color: #ffcb63;
  line-height: 1.9;
}
.globalmenu .closebtn{
  width: 16px;
  margin-top: 14px;
}
.globalmenu .menuitem {
  border-bottom: 1px solid #070715;
  /* margin: 0%; */
  padding: 4% 2%;
  border-top: 1px solid #070715;
  text-align: left;
  margin-top: -1px;

}
.globalmenu .menuitem img{
  width: 13%;
  margin-right: 4%;
}
.globalmenu .menuitem span {
  color: #4970c5;
  font-size: 120%;
}
.hideGlobalMenu {
  right: -60%;
}
.showGlobalMenu {
  right: 0px;
  opacity: 1;
}
.hiddenEye{
  color: #fc8d32;
}
.vissibleEye{
  color:#8ca0c8;
}
.visible-icon {
  font-size: 2rem;
}
.gameLoader {
    position: absolute;
    top: 0px;
    width: 105%;
    height: 100%;
    background-color: rgba(0,0,0,.8);
    left: -5%;
    text-align: center;
    padding-top: 41%;
    color:#fff;
}
.spinner {
  color:#fc8d32;
}
.loaderText{
  font-size:111%;
}
.ticket1, .ticket2{
  opacity: 1;
  transition: 3s all ease;
}
.scale1{
  transform: scale(1);
  transition: 3s all ease;
}

.slide-enter,
.slide-exit {
  transition: transform 1000ms ease-out;
}

.slide-enter {
  transform: translateX(100%);
}

.slide-enter.slide-enter-active {
  transform: translateX(0%);
}

.slide-exit {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  transform: translateX(0%);
}

.slide-exit-active {
  transform: translateX(-100%);
}



@media screen and (min-width: 768px) {
  .housie-number, .housie-number-called {
    width: 56px;
    height: 58px;
    margin: 4px auto !important;
  }
  .housie-number span, .housie-number-called span {
    font-size: 180%;
    line-height: 1.8;
  }
  .betValues img {
    width: 60%;
  }


  .onlinePlayer {
    top: 4.5%;
  }
  .callout {
    margin-left: -3%;
  }
  .betCard2 {
    bottom: 226px;
  }
  .betCard3 {
    bottom: 272px;
  }

  .ticket-number span {
    font-size: 200%;
  }
  .tickerTitle {
    left: -11.5%;
    font-size: 140%;
  }
  .ticketLabel {
    left: -5.5%;
    font-size: 100%;
    bottom: 0;
  }

}
@media screen and (min-width: 1024px) { 
  .betCard2 {
    bottom: 236px;
  }
  .ticket-number span {
    font-size: 250%;
  }
  .tickerTitle {
    left: -9.5%;
  }
}

@media screen and (max-width: 360px) { 
  .housie-number, .housie-number-called {
    width: 24px;
    height: 24px;
    margin: 1px auto !important;
  }
  .housie-number span, .housie-number-called span {
    font-size: 80%;
    display: block;
    line-height: 1.6;
  }
  .userDetails {
    font-size: 110%;
  }
  .onlinePlayer {
    font-size: 80%;
  }
  .housie .menuIcon img {
    width: 24px;
  }
  .betCard3, .betCard2, .betCard {
    transform: scale(.85, .85);
    bottom: 48px;
  }
  .betCard2 {
    bottom: 168px;
  }
  .betCard3 {
    bottom: 196px;
  }
  .gameLogo {
    font-size: 84%;
  }
  .menuIcon img {
    width: 24px;
  }

  .betValues .betvalue {
    left: 21%;
    top: 31%;
    font-size: 46%;
  }
  .globalmenu .menuitem span {
    font-size: 100%;
  }
}
@media screen and (max-height: 670px) { 
   .ticket1{
    margin-top:-5% !important;
  }
  .housie-wrapper{
    margin-top:-3% !important;
  } 
}